export const accessibilityConfigSchema = {
	type: 'object',
	properties: {
		monochrome: {type: 'boolean'}
	}
};

export const dataLabelConfigSchema = {
	type: 'object',
	properties: {
		otherLabel: {type: 'string'},
		fontFamily: {type: 'string'},
		hideLabel: {type: 'boolean'},
		labelType: {type: 'string', enum: ['callout']},
		valueType: {type: 'string', enum: ['none', 'value', 'percent', 'both']},
		labelSize: {type: 'string'},
		labelColor: {type: 'string'},
		valueColor: {type: 'string'},
		showTotal: {type: 'boolean'},
		totalSize: {type: ['string', 'number']},
		totalColor: {type: 'string'},
		totalLabel: {type: 'string'},
		totalLabelSize: {type: ['string', 'number']},
		totalLabelColor: {type: 'string'}
	}
};

export const donutPieConfigSchema = {
	type: 'object',
	properties: {
		x: {type: 'string'},
		y: {type: 'string'},
		colorMap: {type: 'object'},
		colorPalette: {type: 'array', items: {type: 'string'}},
		maxSlices: {type: 'number'}
	}
};

export const donutPieDataPointSchema = {
	type: 'object',
	properties: {
		order: {type: 'number'},
		xxx_percent: {type: 'number'}
	}
};

const registerStyleConfigSchema = {
	type: 'object',
	properties: {
		valueColor: {type: 'string'},
		mutedOpacity: {type: 'number'},
		labelColor: {type: 'string'},
		fontFamily: {type: 'string'},
		fontSize: {type: 'string'},
		fontStyle: {type: 'string'},
		fontWeight: {type: 'string'},
		separator: {type: 'string'},
		xFormat: {type: 'string'},
		yFormat: {type: 'string'}
	}
};

export const registerConfigSchema = {
	type: 'object',
	properties: {
		config: registerStyleConfigSchema,
		orientation: {type: 'string', enum: ['horizontal', 'vertical']},
		expandToFit: {type: 'boolean'},
		legendOnly: {type: 'boolean'},
		showBorder: {type: 'boolean'},
		smallSpacing: {type: 'boolean'},
		oneLine: {type: 'boolean'},
		justifyItems: {type: 'string', enum: ['start', 'center', 'end']},
		disableGrid: {type: 'boolean'},
		hardMute: {type: 'boolean'},
		showPercentages: {type: 'boolean'}
	}
};
