import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	createXenoDomReadyHandler,
	applyPropToChartInstance,
	createXenoPropPreProcessMap,
	buildAndSanitizePropsMaps
} from '@servicenow/library-chart';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {exportChartData} from '@servicenow/library-chart';
import {DonutPie} from 'xenolith/dist/es';
import '@devsnc/sn-chart-screen-reader-table';
import styles from './_now-chart-donut-pie.scss';
import {t} from 'sn-translate';
import themeBehavior from '@servicenow/behavior-theme';
import {
	accessibilityConfigSchema,
	dataLabelConfigSchema,
	donutPieConfigSchema,
	donutPieDataPointSchema,
	registerConfigSchema
} from './schemas/chart-donut-pie-schema';
import {actionTypes} from '@servicenow/ui-core';
import '@servicenow/now-tooltip';
import {
	createTooltipActionHandler,
	schedule,
	unschedule
} from '@servicenow/now-tooltip';
import {configAriaSchema} from '@servicenow/library-schemas';
const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const PROPERTIES = {
	/**
	 * Sets accessibility options.
	 * @type {AccessibilityConfig}
	 */
	accessibilityConfig: {xenolith: true, schema: accessibilityConfigSchema},
	/**
	 * When true, enables accessibility options for the chart. See `accessibilityConfig` for available options.
	 * @type {boolean}
	 */
	accessibilityMode: {
		xenolith: true,
		default: false,
		schema: {type: 'boolean'}
	},
	/**
	 * An object whose keys reference a specific HTML element within the
	 * component. The value of each key is an object that contains
	 * an ARIA property (or properties) and corresponding value/s set on the specified element.
	 * The 'chart' key corresponds to the inner html `<div>` element in
	 * the component that wraps the chart and the register. Use this property to
	 * supply an aria-label that describes the chart for screen readers. See
	 * https://www.w3.org/TR/wai-aria-1.1/ for other properties and accepted values.
	 * @type {{ 'aria-*': string }}
	 */
	configAria: {
		schema: {
			anyOf: [
				{
					type: 'object',
					properties: {
						chart: configAriaSchema
					},
					additionalProperties: false
				},
				configAriaSchema
			]
		}
	},
	/**
	 * The chart's height in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the height of the parent DOM element.
	 * @type {'auto' | number}
	 */
	height: {
		xenolith: true,
		schema: {oneOf: [{const: 'auto'}, {type: 'number'}]}
	},
	/**
	 * The chart's width in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the width of the parent DOM element.
	 * @type {'auto' | number}
	 */
	width: {
		xenolith: true,
		schema: {oneOf: [{const: 'auto'}, {type: 'number'}]}
	},
	/**
	 * Data label when the `showDataLabels` property is true. Includes the strategy for generating
	 * labels, type styling, and alignment options.
	 * @type {DataLabelConfig}
	 */
	dataLabelConfig: {xenolith: true, schema: dataLabelConfigSchema},
	/**
	 * Extra space around the chart body in addition to
	 * the space allocated for the axes.
	 * @type {{left: number, right: number, top: number, bottom: number}}
	 */
	extraMargin: {
		xenolith: true,
		schema: {
			type: 'object',
			properties: {
				left: {type: 'number'},
				right: {type: 'number'},
				top: {type: 'number'},
				bottom: {type: 'number'}
			}
		}
	},
	/**
	 * When true, hides the register that appears beside the chart.
	 * @type {boolean}
	 */
	hideRegister: {xenolith: true, default: false, schema: {type: 'boolean'}},
	/**
	 * Configures time and number formatting for different languages.
	 * Specify the locale using one of the following two-part `language-COUNTRY` IETF codes:
	 * `ar-AE`, `ar-BH`, `ar-DJ`, `ar-DZ`, `ar-EG`, `ar-EH`, `ar-ER`, `ar-IL`, `ar-IQ`, `ar-JO`, `ar-KM`, `ar-KW`, `ar-LB`, `ar-LY`, `ar-MA`,
	 * `ar-MR`, `ar-OM`, `ar-PS`, `ar-QA`, `ar-SA`, `ar-SD`, `ar-SO`, `ar-SS`, `ar-SY`, `ar-TD`, `ar-TN`, `ar-YE`,
	 * `ca-ES`, `cs-CZ`, `de-CH`, `de-DE`, `en-CA`, `en-GB`, `en-IN`, `en-US` (default), `es-ES`, `es-MX`, `fi-FI`, `fr-CA`, `fr-FR`,
	 * `he-IL`, `hu-HU`, `it-IT`, `ja-JP`, `ko-KR`, `mk-MK`, `nl-NL`, `pl-PL`, `pr-BR`, `ru-RU`, `sv-SE`, `uk-UA`, `zh-CN`
	 * @type {string}
	 */
	locale: {xenolith: true, schema: {type: 'string'}},
	/**
	 * Name of the series to mute on the chart. The component removes the pie segment corresponding to the given series,
	 * resizes the remaining pie slices, and recalculates percentages and totals.
	 * @type {string | Array<string> | Set<string>}
	 */
	mutedSeries: {
		xenolith: true,
		schema: {
			oneOf: [
				{type: 'string'},
				{type: 'array', items: {type: 'string'}},
				{type: 'object'}
			]
		}
	},
	/**
	 * Configures options for the register displayed beside the chart.
	 * @type {RegisterConfig}
	 */
	registerConfig: {xenolith: true, schema: registerConfigSchema},
	/**
	 * Sets the position and orientation of the register in relation to the chart. If you pass in the values `top` or `bottom`,
	 * the register displays horizontally. If you pass in the values `left` or `right`,
	 * the register displays vertically. These orientations override values in the `registerConfig` property.
	 * @type {('top'|'bottom'|'left'|'right')}
	 */
	registerPosition: {
		xenolith: true,
		schema: {type: 'string', enum: ['top', 'bottom', 'left', 'right']}
	},
	/**
	 * Configures the chart. Donut pie charts only have one series, so this property only has
	 * one key-value pair.
	 * @type {Object.<string, DonutPieConfig>}
	 */
	seriesConfig: {
		xenolith: true,
		accessibilityTable: true,
		schema: {type: 'object', patternProperties: {'.*': donutPieConfigSchema}}
	},
	/**
	 * The array of data points defining the main plotted element on the chart.
	 * Works in conjunction with the `seriesConfig` property.
	 * @type {DonutPieDataPoint[]}
	 */
	seriesData: {
		xenolith: true,
		accessibilityTable: true,
		schema: {type: 'array', items: donutPieDataPointSchema}
	},
	/**
	 * When true, shows labels for data values on the chart. Works in conjunction with the `dataLabelConfig`
	 * property, which controls how the labels appear on the chart.
	 * @type {boolean}
	 */
	showDataLabels: {xenolith: true, default: false, schema: {type: 'boolean'}},
	/**
	 * When true, shows a tooltip on hover that contains the closest data points from each
	 * series. Works in conjunction with the `tooltipConfig` property, which controls how the tooltip appears.
	 * @type {boolean}
	 */
	showTooltip: {xenolith: true, default: false, schema: {type: 'boolean'}},
	/**
	 * Configures the register, which displays values in a tooltip when hovering
	 * over the chart.
	 * @type {RegisterConfig}
	 */
	tooltipConfig: {xenolith: true, schema: registerConfigSchema},
	/**
	 * Format string or function to display y values on the chart and axes.
	 * - Strings are passed to d3 and must conform to their number formatting code. See [Number Formats](https://github.com/d3/d3/blob/master/API.md#number-formats-d3-format).
	 * - Functions must accept a number and return a string.
	 * @type {string | function}
	 */
	yFormat: {xenolith: true, accessibilityTable: true},
	/**
	 * Defines custom actions dispatched on mouse events.
	 * Values can either be:
	 * - A String of preconfigured actions defined below.
	 * - An `actionConfig` object with a possible dispatch:
	 * ```
	 * {
	 *  eventName: 'click' // Name of the DOM event to subscribe to.
	 *  getCoordsFn: 'getMouseCoords' // Either a string of a predefined function, nothing, or a custom function that gets the mouse coordinates and returns coordinates to use.
	 *  getDataFn: 'getClosestData' // Either a string of a predefined function or a custom function that gets the coordinates from the previous callback and returns a data set.
	 *  processDataFn: 'displayFoundData' // Either a string of a predefined function or a custom function that geta both the coordinates and the data returned by the previous callback. Can also be an array of string/functions invoked in order.
	 *  dispatchName: 'NOW_CHART_DONUT_PIE#MY_CLICK' // If provided, an action with the configured name is dispatched with the result of both `getCoordsFn` and `getDataFn`.
	 * }
	 * ```
	 *
	 * This chart doesn't have a predefined `getCoordsFn` or `getDataFn`, but
	 * automatically provides coords and data to the `processDataFn`.
	 *
	 * The following preconfigured interactions are available:
	 * - `getDataOnClick`: Dispatches a `NOW_CHART_DONUT_PIE#CLICK` action with coords and data on click.
	 *
	 * @type {Array<string | ActionConfig>}
	 */
	interactions: {
		xenolith: true,
		schema: {
			type: 'array',
			items: {
				oneOf: [
					{
						type: 'string',
						enum: ['getDataOnClick']
					},
					{
						type: 'object',
						properties: {
							eventName: {type: 'string'},
							dispatchName: {type: 'string'}
						}
					}
				]
			}
		}
	},
	/**
	 * When true, displays a screen-readable table below the chart for accessibility.
	 * @type {boolean}
	 */
	accessibilityTable: {default: false, schema: {type: 'boolean'}},
	/**
	 * The radius of the circle used to draw the pie. By default, an appropriate radius is
	 * calculated based on the available space.
	 * @type {number}
	 */
	radius: {xenolith: true, schema: {type: 'number'}},
	/**
	 * The inner radius of the circle used to draw a `donut` or `semidonut` type chart, expressed
	 * as a fraction of the `radius`, from 0 to a maximum of 0.95.
	 * By default, an appropriate innerRadius is calculated based on the available space.
	 * @type {number}
	 */
	innerRadius: {xenolith: true, schema: {type: 'number'}},
	/**
	 * Specifies the order in which to draw the slices.
	 * - `alphabetical`: Draws the slices in alphabetical order by label text.
	 * - `dataOrder`: Draws the slices in the same order as presented in the `seriesData` property.
	 * - `size`: Draws the slices in the order of largest to smallest. This is the default setting.
	 * @type {('alphabetical'|'dataOrder'|'size')}
	 */
	orderBy: {
		xenolith: true,
		schema: {type: 'string', enum: ['alphabetical', 'dataOrder', 'size']}
	},
	/**
	 * Number of characters in chart and register labels before they are truncated.
	 * Default: 0, no truncation.
	 * @type {number}
	 */
	labelTruncation: {xenolith: true, schema: {type: 'number'}},
	/**
	 * Unit of measure for y values.
	 * If provided, the unit is appended to the end of all y values on the chart.
	 * @type {string}
	 */
	yUnit: {xenolith: true, schema: {type: 'string'}},
	/**
	 * Specifies the type of chart to draw.
	 * @type {('pie'|'donut'|'semidonut')}
	 */
	type: {
		xenolith: true,
		schema: {type: 'string', enum: ['pie', 'donut', 'semidonut']}
	},
	/**
	 * Caption for the accessibility data table.
	 * @type {string}
	 */
	accessibilityTableCaption: {
		accessibilityTable: true,
		schema: {type: 'string'}
	}
};

const {XENOLITH_PROPERTIES, SR_TABLE_PROPERTIES} = buildAndSanitizePropsMaps(
	PROPERTIES
);
const INTERACTIONS_MAP = {
	getDataOnClick: [
		{
			eventName: 'click',
			dispatchName: 'NOW_CHART_DONUT_PIE#CLICK'
		}
	]
};
const preProcessMap = createXenoPropPreProcessMap();
preProcessMap.interactions.processArgs = INTERACTIONS_MAP;
preProcessMap.dataLabelConfig = {
	xenoPropName: 'dataLabelConfig',
	process: function({value}) {
		return {
			otherLabel: t('Other'),
			...value
		};
	}
};

// array of properties to observe, where each element has the `xenolith` name, and reactions is an array of steps to take when the observed
// property changes. Reactions are processed in order. Can be one of three types:
//   dispatch: dispatch an action, args -> `type` and optional `processValue` function. payload has the (optionally processed) updated value.
//   updateState: update this components state. args -> `operation`, `path` and optional `processValue` function. component of state is updated with (optionally processed) value
//   updateProperties: update this components property. args -> `operation`, `propName` and optional `processValue` function. component of state is updated with (optionally processed) value
const OBSERVED_PROPERTIES = [
	{
		xenolith: 'requestTooltip',
		reactions: [
			{
				// Cannot use updateState because the tooltip need to use schedule.
				type: 'dispatch',
				args: {type: 'PRIVATE#TOOLTIP_REQUEST'}
			}
		]
	}
];

const showTooltip = (updateState, {payload, stopPropagation}) => {
	let value;
	stopPropagation();

	if (!payload) {
		value = {content: '', opened: false};
	} else {
		const {targetRef, textContent} = payload;
		value = {
			targetRef,
			content: textContent,
			opened: true
		};
	}

	updateState({
		path: 'infoTooltip',
		operation: 'set',
		value
	});
};

const buildSRTExportOptions = (properties) => {
	const {yFormat, yUnit} = properties;

	const exportType = 'flatTable';
	const dataStreams = [
		{configKey: 'seriesConfig', dataKey: 'seriesData', coords: ['y']}
	];
	const aggregators = null;
	const formatters = {
		x: {format: null, scale: 'ordinal', unit: null},
		y: {format: yFormat, scale: 'linear', unit: yUnit}
	};
	return {exportType, dataStreams, aggregators, formatters};
};
// if data labels will show percentages, then include them in the table
const buildSRTExportProps = (properties) => {
	// unless they explicitly don't want percentages, show them
	const {dataLabelConfig = {valueType: 'both'}} = properties;
	if (dataLabelConfig.valueType === 'value') return properties;
	// just check the first data point, assume all the rest have the same props
	const {seriesConfig, seriesData} = properties;
	if (!seriesData || seriesData.length === 0) return properties;
	const firstConfig = Object.values(seriesConfig)[0];
	const needPercentages = seriesData.reduce(
		(acc, dp) => acc || typeof dp[`${firstConfig.y}_percent`] === 'undefined',
		false
	);
	if (!needPercentages) return properties;
	const pieSum = seriesData.reduce((acc, dp) => acc + dp[firstConfig.y], 0);
	// need to add percentages to data points. we have the series sums, so just add in a _percent field to each data point
	const newData = seriesData.map((d) => {
		const newDP = {...d};
		for (const series of Object.values(seriesConfig)) {
			const nextYKey = series.y;
			if (typeof d[`${nextYKey}_percent`] !== 'number') {
				const dValue = typeof d[nextYKey] === 'number' ? d[nextYKey] : 0;
				newDP[`${nextYKey}_percent`] = pieSum
					? Math.round((dValue * 100) / pieSum)
					: 0;
			}
		}
		return newDP;
	});

	// substitute new data, with percentages
	return {...properties, seriesData: newData};
};

const buildAccessibilityTableOptions = (props) => {
	const tableOptions = {};
	for (const name of SR_TABLE_PROPERTIES.values()) {
		tableOptions[name] = props[name];
	}
	return tableOptions;
};

/** @seismicView now-chart-donut-pie */
const view = (state) => {
	const {properties, infoTooltip, chartInstance} = state;
	const {accessibilityTable} = properties;
	const {content, targetRef, opened} = infoTooltip;

	/*
    Must 1) wait for the chartInstance / behavior to be instatiated
    2) only apply the theme if it is different or else it triggers a Xenolith render.
  */
	if (
		chartInstance &&
		state.behaviors.theme &&
		JSON.stringify(state.behaviors.theme) !==
			JSON.stringify(chartInstance.theme)
	) {
		chartInstance.theme = state.behaviors.theme;
	}

	return (
		<Fragment>
			<div id="chart-container" />
			{accessibilityTable ? (
				<div className="now-chart-screen-reader-table-parent">
					<sn-chart-screen-reader-table
						options={buildAccessibilityTableOptions(properties)}
						data={exportChartData(
							chartInstance,
							buildSRTExportProps(properties),
							buildSRTExportOptions(properties)
						)}
						className="now-chart-screen-reader-table"
					/>
				</div>
			) : null}
			<now-tooltip
				id="tooltip"
				manage-opened
				content={content}
				target-ref={targetRef}
				opened={opened}
				delay={{show: 0, hide: 0}}
			/>
		</Fragment>
	);
};

/**
 * Chart in the shape of a pie, donut, or semi-donut, that displays values as parts that represent a whole.
 *
 * ```jsx
 * const options = {
 *   type: "donut",
 *   width: "auto",
 *   height: "auto",
 *   seriesConfig: {
 *     series1: {
 *      x: "name",
 *      y: "count"
 * 	 }
 *   },
 *   seriesData: [
 * 	   {name: "IPA", count: 5},
 *     ...otherData
 *   ],
 *   showDataLabels: true
 * }
 * <now-chart-donut-pie {...options} />
 * ```
 *
 * @seismicElement now-chart-donut-pie
 * @summary Circular chart that displays collected values as parts that represent a whole.
 */
createEnhancedElement('now-chart-donut-pie', {
	properties: PROPERTIES,
	initialState: {
		infoTooltip: {targetRef: null, content: '', opened: false}
	},
	view,
	styles,
	behaviors: [
		{
			behavior: themeBehavior,
			options: {
				items: [
					'type.fontFamily',
					'global.*',
					'datavis.*',
					'menu.backgroundColor',
					'menu.borderRadius'
				]
			}
		}
	],
	actionHandlers: {
		...createTooltipActionHandler((updateState) => {
			updateState({
				path: 'infoTooltip',
				operation: 'set',
				value: {ref: null, content: '', opened: false}
			});
		}),
		[COMPONENT_DOM_READY]: createXenoDomReadyHandler(
			{
				xenoPropList: XENOLITH_PROPERTIES,
				preProcessMap,
				observedProps: OBSERVED_PROPERTIES
			},
			({host, state}) => {
				const chartContainer = host.shadowRoot.getElementById(
					'chart-container'
				);
				return new DonutPie({
					node: chartContainer,
					theme: state.behaviors.theme
				});
			}
		),
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			dispatch,
			updateState,
			host,
			properties
		}) => {
			const {name, value, previousValue} = action.payload;
			applyPropToChartInstance({
				chartInstance: state.chartInstance,
				value,
				previousValue,
				name,
				xenoPropList: XENOLITH_PROPERTIES,
				dispatch,
				preProcessMap,
				state,
				updateState,
				host,
				properties
			});
		},
		[COMPONENT_DISCONNECTED]: ({action, state}) => {
			unschedule(action.meta.id);
			state.chartInstance.destroy();
		},
		'PRIVATE#TOOLTIP_REQUEST': {
			stopPropagation: true,
			effect: ({action, updateState}) => {
				schedule(action.meta.id, showTooltip.bind(null, updateState, action));
			}
		}
	}
});
